import { getComments,addComment ,deleteComment} from "@/api/article.js";
export default {
  name: "ArticleComment",
  props: {
    article: {
      type: Object,
      required: true,
    },
    user: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      // 文章列表
      comments: [],
      body: "",
    };
  },
  mounted() {
    // 不要求SEO，请求数据放到mounted里面，只走客户端渲染
    this.getComments();
  },
  components: {},
  conputed: {},
  methods: {
    async getComments() {
      try {
        const { data } = await getComments(this.article.slug);
        this.comments = data.comments;
      } catch (err) {
        console.log(err);
      }
    },
    async addComment(){
      if(!this.body) return
      try{
        const {data} = await addComment(this.article.slug,this.body)
        if(data.comment){
          this.comments.unshift(data.comment)
          this.body = ''
        }
     }catch(err){
       console.log(err)
     }
    },
    async deleteComment(commentId){
      try{
        const {data} = await deleteComment(this.article.slug,commentId)
        if(JSON.stringify(data)==="{}"){
          this.comments = this.comments.filter(f=> f.id!==commentId)
        }
     }catch(err){
       console.log(err)
     }
    },
  },
};
