import {
  addFollow,
  deleteFollow,
  deleteFavorite,
  addFavorite,
  deleteArticle
} from "@/api/article.js";
export default {
  name: "ArticleMeta",
  props: {
    article: {
      type: Object,
      required: true,
    },
    username: {
      type: String,
      required: true,
    },
  },
  data() {
    return {};
  },
  methods: {
    async onFollow(article) {
      try {
        if (!this.username) return this.$router.push("/login");
        article.followDisabled = true; // 禁用点击
        if (article.author.following) {
          // 取消关注
          await deleteFollow(article.author.username);
          article.author.following = false;
        } else {
          // 添加关注
          await addFollow(article.author.username);
          article.author.following = true;
        }
        article.followDisabled = false; // 允许点击
      } catch (err) {
        console.log(err);
      }
    },
    async onFavorite(article) {
      try {
        if (!this.username) return this.$router.push("/login");
        article.favoriteDisabled = true; // 禁用点击
        if (article.favorited) {
          // 取消点赞
          await deleteFavorite(article.slug);
          article.favorited = false;
          article.favoritesCount -= 1;
        } else {
          // 添加点赞
          await addFavorite(article.slug);
          article.favorited = true;
          article.favoritesCount += 1;
        }
        article.favoriteDisabled = false; // 允许点击
      } catch (err) {
        console.log(err);
      }
    },
    async deleteArticle(article){
      try{
        article.deleteDisabled=true
        const {data} = await deleteArticle(article.slug);
        if(JSON.stringify(data)==="{}"){
          this.$router.push('/')
        }else{
          console.log('出错了');
        }
        article.deleteDisabled=false
      }catch(err){
         console.log(err);
      }
    }
  },
};
